
.sort_head,
.sort_box{
  width: 100%;
  height: 14vw;
  line-height: 14vw;
}
.sort_box{
  position: fixed;
  left: 0;
  top: 0;
  display: -ms-flexbox;
  display: flex;
  display: -webkit-box;
  display:-webkit-flex;
  background: #fff;
  border-bottom: 1px solid #e4e4e4;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.sort_box .sort_item{
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
  font-size: 14px;
  color: #333;
}
.sort_item .item_down{
  display:inline-block;
  margin-left: 3px;
  vertical-align: -2px;
  font-size: 18px;
  color: #999;
  transform: rotate(90deg);
  -webkit-transform: rotate(90deg);
}
.sort_item.active{
  color: #00b934
}



