
.sort_block{
  position: fixed;
  width: 100%;
  left: 0;
  top:14vw;
  right: 0;
  bottom: 0;
  background: rgba(0,0,0,.4);
}
.sort_view{
  padding: 20px;
  background: #fff;
  overflow: hidden;
}
.sort_view .item{
  float: left;
  margin: 5px 10px;
  font-size: 13px;
  color: #666;
  height: 30px;
  line-height: 30px;
  padding: 0 16px;
  border:1px solid #e4e4e4;
  border-radius: 18px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.item.active{
    color: #fff;
    background: #00b934;
    border-color: #00b934;
}



