
.active_head{
    padding: 0 20px;
    height: 44px;
    line-height: 44px;
    font-size: 16px;
    color: #333;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.active_item {
    padding:0 20px 10px;
}
.active_more{
    float: right;
    font-size: 13px;
    color: #999;
}
.item_pic{
    width: 100%;
    height: 32vw;
}
.item_title{
    margin-top: 6px;
    width: 100%;
    font-size: 13px;
    color: #333;
    overflow:hidden; 
    text-overflow:ellipsis;
    display:-webkit-box; 
    -webkit-box-orient:vertical;
    -webkit-line-clamp:2;
}
.item_date{
    margin-top: 2px;
    font-size: 12px;
    color: #999;
}
.item_more{
    position: relative;
    width: 100%;
    height: 32vw;
    background: #eee;
    text-align: center;
}
.more_txt{
    position: absolute;
    width: 100%;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
    font-size: 13px;
    color: #999;
}
.more_txt span{
    padding-bottom: 2px;
    border-bottom: 1px solid #ccc;
}



